            let token;

            if (!host) {
                token = this.google.access_token;
            } else {
                for (let key in this.oauthTokens) {
                    const regex = wildcardToRegExp(key);
                    if (regex.test(host)) {
                        token = this.oauthTokens[key];
                        break;
                    }
                }
            }

            return token;

